.Header {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  padding: 10px;
  height: 50px;

  &__icon {
    width: 50px;
    height: 50px;
    background-image: url('../../../public/icons/chat-icon.svg');
    background-size: cover;
